import { useState } from "react";
import Nav from "../../components/Navbar/Nav";
import "./signup.css";
import { NavLink } from "react-router-dom";
import { RxAvatar } from "react-icons/rx";

import axios from "axios";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";

const Signup = () => {
  const [userName, SetUserName] = useState("");
  const [email, SetEmail] = useState("");
  const [password, SetPassword] = useState("");
  const [phone, SetPhone] = useState("");
  const [address, SetAddress] = useState("");
  const [answer, SetAnswer] = useState("");
  const [confirmPassword, SetConfirmPassword] = useState("");
  const [userPhoto, SetUserPhoto] = useState(null);
  const [loading, SetLoading] = useState(false);
  const Navigate = useNavigate();

  const handleFileInputChange = (e) => {
    const file = e.target.files[0];
    SetUserPhoto(file);
  };

  const HandleSubmit = async (e) => {
    try {
      e.preventDefault();
      SetLoading(true);

      const user = new FormData();
      user.append("userName", userName);
      user.append("email", email);
      user.append("password", password);
      user.append("phone", phone);
      user.append("address", address);
      user.append("answer", answer);
      user.append("userPhoto", userPhoto);

      user.append("confirmPassword", confirmPassword);

      let res = await axios.post("/api/auth/signup", user);

      if (res?.data?.success) {
        toast.success(res.data.message);
        Navigate("/login");
        SetLoading(false);
      } else {
        SetLoading(false);
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      SetLoading(false);
      toast.error("something went wrong!");
    }
  };
  return (
    <div>
      <Nav />

      <div className="center " style={{ width: "700px", marginTop: "12rem" }}>
        <h1>Sign up</h1>

        <form>
          <div className="d-flex justify-content-between">
            <div className="txt_field">
              <input
                type="text"
                name="userName"
                required
                autoComplete="off"
                value={userName}
                onChange={(e) => SetUserName(e.target.value)}
              />

              <span></span>
              <label>UserName</label>
            </div>
            <div className="txt_field">
              <input
                type="Number"
                name="phone"
                required
                autoComplete="off"
                value={phone}
                onChange={(e) => SetPhone(e.target.value)}
              />

              <span></span>
              <label>Phone</label>
            </div>
          </div>

          <div className="txt_field">
            <input
              type="text"
              name="address"
              required
              autoComplete="off"
              value={address}
              onChange={(e) => SetAddress(e.target.value)}
            />

            <span></span>
            <label>Address</label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name="answer"
              required
              autoComplete="off"
              value={answer}
              onChange={(e) => SetAnswer(e.target.value)}
            />

            <span></span>
            <label>
              {" "}
              what is your favourite sport name{" "}
              <span className="text-italic h6"> (security question ) </span>
              <sup>*</sup>
            </label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name="email"
              required
              autoComplete="off"
              value={email}
              onChange={(e) => SetEmail(e.target.value)}
            />
            <span></span>
            <label>email</label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name="password"
              required
              autoComplete="off"
              value={password}
              onChange={(e) => SetPassword(e.target.value)}
            />
            <span></span>
            <label>password</label>
          </div>
          <div className="txt_field">
            <input
              type="text"
              name="confirmPassword"
              required
              value={confirmPassword}
              autoComplete="off"
              onChange={(e) => {
                SetConfirmPassword(e.target.value);
              }}
            />
            <span></span>
            <label> confirm password</label>
          </div>
          <NavLink className="nav-link pass" exact to="/forgetpassword">
            forgetpassword?
          </NavLink>
          <div className="mt-3 p-3  ms-5 ">
            {userPhoto ? (
              <img
                src={URL.createObjectURL(userPhoto)}
                alt="userAvatar"
                className="  avatar"
              />
            ) : (
              <RxAvatar className="h1 " />
            )}
            <label htmlFor="file-input" className=" p-2  ms-3 fileupload">
              {" "}
              <span>upload a file</span>
            </label>
            <input
              type="file"
              name="avatar"
              id="file-input"
              onChange={handleFileInputChange}
              accept=".jpg, .png, .jpeg"
              className="input_file"
            />
          </div>

          <button role="submit" onClick={HandleSubmit}>
            {loading ? "please wait" : "signUp"}
          </button>s
        </form>
      </div>
    </div>
  );
};

export default Signup;
